body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fullscreen-loading {
    position: fixed;
    z-index: 999;
    height: 2em;
    width: 2em;
    overflow: show;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }


/* Transparent Overlay */
.fullscreen-loading:before {
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: -webkit-radial-gradient(rgba(20, 20, 20,.8), rgba(0, 0, 0, .8));
    background: radial-gradient(rgba(20, 20, 20,.8), rgba(0, 0, 0, .8));
  
    background: -webkit-radial-gradient(rgba(20, 20, 20,.8), rgba(0, 0, 0,.8));
  }
.homes-container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    background: #fff;
    padding: 1rem 1rem;
}

.home-table table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
}

.home-table th {
    color: #3F51B5;
}

.home-table td {
    color: #565859;
    font-family: 'Times New Roman', Times, serif
}

.home-table th, td {
    text-align: left;
    padding: 11px;
    border: 1px solid #ddd;
}

.icon-color-red {
    color: red;
    size: 11px;
}

.icon-color-green {
    color: green;
}

.text-right {
    text-align: right;
}

.home-table tr:nth-child(even){background-color: #f2f2f2}

.home-table tbody tr:hover {
    background-color: #E6F7FF;
    cursor: pointer;
}

.hideMe {
    -webkit-animation: cssAnimation 0s ease-in 1s forwards; 
    animation: cssAnimation 0s ease-in 1s forwards;
}
@keyframes cssAnimation {
    0%   {opacity: 1;}
    90%  {opacity: 1;}
    100% {opacity: 0;}
}
@-webkit-keyframes cssAnimation {
    0%   {opacity: 1;}
    90%  {opacity: 1;}
    100% {opacity: 0;}
}
